.mainWrapper {
    display: flex;
}

.menu {
    width: 160px;
    height: 100vh;
    padding-left: 10px;
    background-color: rgb(44, 44, 44);
    display: flex;
    flex-direction: column;
}

.menu a {
    text-decoration: none;
    font-size: 30px;
    font-weight: 600;
    color: white;
}

.active {
    color: white;
    text-shadow: 1px 1px 2px rgb(0, 0, 0), 0 0 25px rgb(255, 251, 0), 0 0 5px rgb(137, 139, 0);
}

.content-wrap {
    background-color: rgb(12, 11, 11);
    color: rgb(189, 189, 189);
    height: calc(100vh - 50px);
}

.content-wrap.planets {
    height: 100vh;
    width: calc(100vw - 160px);
    ;
}

.text-wrap {
    padding: 10px 10%;
}

.main-page {
    width: calc(100vw - 160px);
    flex-direction: column;
}

.main-menu {
    height: 50px;
    background-color: rgb(250, 225, 6);
}

.personShip {
    padding-left: 30px;
}

.planets-btn {
    background-color: rgb(250, 225, 6);
    width: 100%;
    height: 40px;
    border-radius: 2px;
    margin-bottom: 40px;
}

.pagination li {
    display: inline-block;
}

.pagination a {
    padding: 10px;
    border-radius: 10%;
}

.pagination .active a {
    background-color: rgb(44, 44, 44);
}

.search-textarea {
    float: right;
}

.search-block {
    background-color: rgb(250, 225, 6);
    color: rgb(0, 0, 0);
    padding: 20px;
    float: right;
    border-radius: 5px;
    margin-top: -5px;
}
.search-block ul li {
    cursor: pointer;
}
.search-block ul li:hover{
    text-shadow: 2px 2px 4px #000000;
}
.clock {
    margin: -15px 0 -15px 0;
}